import Slider from "react-slick";

import climainovasi from "./img/certificate/sertifikat_lima_inovasi.jpg";
import cabsen from "./img/certificate/sertifikat_absen.jpg";
import csanbercodeds from "./img/certificate/sertifikat_sanbercode_data_science.jpg";
import cdtspython from "./img/certificate/sertifikat_dts_python.jpg";
import cdtsandroid from "./img/certificate/sertifikat_dts_android_developer.jpg";
import cdtsfacebook from "./img/certificate/sertifikat_dts_facebook.jpg";
import cdicodingaws from "./img/certificate/sertifikat_dicoding_aws.jpg";
import cdicodingpython from "./img/certificate/sertifikat_dicoding_python1.jpg";
import cdicodingvd from "./img/certificate/sertifikat_dicoding_visualisasi_data.jpg";
import cdicodingml from "./img/certificate/sertifikat_dicoding_ml.jpg";
import csatgana from "./img/certificate/sertifikat_satgana.jpg";
import clinkedinds from "./img/certificate/sertifikat_linkedin_ds.jpg";
import calterrajs from "./img/certificate/sertifikat_alterra_javascript101.jpg";
import calterrabackend from "./img/certificate/sertifikat_alterra_backend101.jpg";
import cgdscgit from "./img/certificate/sertifikat_gdsc_git_workflow.jpg";
import cgdscpythonoop from "./img/certificate/sertifikat_gdsc_python_oop.jpg";
import cgdscsdlc from "./img/certificate/sertifikat_gdsc_sdlc.jpg";
import cgdscccda from "./img/certificate/sertifikat_gdsc_ccda.jpg";
import crevouda from "./img/certificate/sertifikat_revou_intro_data_analytics.jpg";
import cbitgree from "./img/certificate/sertifikat_bitdegree.jpg";
import ckollads from "./img/certificate/sertifikat_kolla_ds.jpg";

const Certificate = () => {
    const settings = {
        infinite: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        variableWidth: true,
        autoplay: true,
        autoplaySpeed: 2000,
        pauseOnHover: true,
        centerMode: true
      };

    const certificate = [
        {'id':1, 'url':climainovasi, 'name':'Sertifikat LIMA Inovasi'},
        {'id':2, 'url':cabsen, 'name':'Sertifikat Absen'},
        {'id':3, 'url':cdtspython, 'name':'Sertifikat DTS Python'},
        {'id':4, 'url':csanbercodeds, 'name':'Sertifikat Sanbercode'},
        {'id':5, 'url':cdtsfacebook, 'name':'Sertifikat DTS Facebook'},
        {'id':6, 'url':cbitgree, 'name':'Sertifikat Bitdegree'},
        {'id':7, 'url':cdtsandroid, 'name':'Sertifikat DTS Android'},
        {'id':8, 'url':ckollads, 'name':'Sertifikat Kolla DS'},
        {'id':9, 'url':cdicodingaws, 'name':'Sertifikat Dicoding AWS'},
        {'id':10, 'url':cdicodingpython, 'name':'Sertifikat Dicoding Python'},
        {'id':11, 'url':cdicodingvd, 'name':'Sertifikat Dicoding Visualisasi Data'},
        {'id':12, 'url':cdicodingml, 'name':'Sertifikat Dicoding ML'},
        {'id':13, 'url':csatgana, 'name':'Sertifikat Satgana UX'},
        {'id':14, 'url':clinkedinds, 'name':'Sertifikat Linkedin Data Science'},
        {'id':15, 'url':calterrajs, 'name':'Sertifikat Alterra Javascript 101'},
        {'id':16, 'url':calterrabackend, 'name':'Sertifikat Alterra Backend 101'},
        {'id':17, 'url':crevouda, 'name':'Sertifikat Mini Revou Data Analytics'},
        {'id':18, 'url':cgdscgit, 'name':'Sertifikat GDSC Git Workflow'},
        {'id':19, 'url':cgdscpythonoop, 'name':'Sertifikat GDSC Python OOP'},
        {'id':20, 'url':cgdscsdlc, 'name':'Sertifikat GDSC SDLC'},
        {'id':21, 'url':cgdscccda, 'name':'Sertifikat GDSC CCDA'}
    ]
    
    return(
        <div id="certificate" className="py-5 bg-light">
            <div className="container py-5">
                <div className="mb-5">
                    <div className="text-title text-center mb-5">Certifications</div>
                    <Slider {...settings}>
                        {certificate.map((certificate) => (
                            <div key={certificate.id}><img src={certificate.url} alt={certificate.name} loading="lazy"></img></div>
                        ))}
                    </Slider>
                </div>
            </div>
        </div>
    )
}

export default Certificate;